import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import PhraseItem from '../PhraseItem';
import { db } from '../../../../utils/firebase';
import { selectPhrases, setPhrases } from '../../phraseSlice';
import PhraseModal from '../../components/PhraseModal';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  list: {
    flexGrow: 1,
  },
  gridList: {},
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));

export default function PhraseList() {
  const phrases = useSelector(selectPhrases);
  const [phrase, setPhrase] = useState();
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);

  const handleOpen = (phrase) => {
    setPhrase(phrase);
    setOpen(true);
  };

  const handleDelete = async (phrase) => {
    await db.collection('phrases').doc(phrase.id).delete();
    return db
      .collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      ) // .then(console.log);
      .then((phrases) => dispatch(setPhrases(phrases)));
  };

  const handleClose = () => {
    setOpen(false);
  };

  const updatePhrase = async (data, phrase) => {
    await db.collection('phrases').doc(phrase.id).set(data, { merge: true });
    setOpen(false);
    return db
      .collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      )
      .then((phrases) => dispatch(setPhrases(phrases)));
  };
  const loadPhrase = () => {
    db.collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      ) // .then(console.log);
      .then((phrases) => dispatch(setPhrases(phrases)));
  };
  useEffect(() => {
    loadPhrase();
  }, []);

  return (
    <div className={classes.root}>
      <Grid container className={classes.list} spacing={2}>
        <Grid item xs={12}>
          <Grid container justify='center' spacing={2}>
            {phrases.map((phrase) => (
              <PhraseItem
                phrase={phrase}
                key={phrase.id}
                handleOpen={handleOpen}
                handleDelete={handleDelete}
              />
            ))}
          </Grid>
        </Grid>
      </Grid>
      <PhraseModal
        open={open}
        handleClose={handleClose}
        phrase={phrase}
        updatePhrase={updatePhrase}
      />
    </div>
  );
}
